import React from "react";
import logo from './logo.svg';
import './App.css'

class App extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      ItemList: [],
      activeItem: {
        id: null,
        title: "",
        isDone: false
      },
      isEditing: false
    }
    this.fetchItems = this.fetchItems.bind(this)
    this.handleChange = this.handleChange.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)
    this.getCookie = this.getCookie.bind(this)
    this.startEdit = this.startEdit.bind(this)
    this.handleDelete = this.handleDelete.bind(this)
    this.itemCompleted = this.itemCompleted.bind(this)
  }

  componentDidMount() {
    this.fetchItems()
  }

  getCookie(name) {
    let cookieValue = null;
    if (document.cookie && document.cookie !== '') {
      const cookies = document.cookie.split(';');
      for (let i = 0; i < cookies.length; i++) {
        const cookie = cookies[i].trim();
        // Does this cookie string begin with the name we want?
        if (cookie.substring(0, name.length + 1) === (name + '=')) {
          cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
          break;
        }
      }
    }
    return cookieValue;
  }
  fetchItems() {
    fetch("https://todo-app-react-django.herokuapp.com/api/item/")
      .then(response => response.json())
      .then(data => this.setState({ ItemList: data }));
  }

  startEdit(item) {
    this.setState({
      activeItem: item,
      isEditing: true
    })

  }

  handleChange(e) {
    let name = e.target.name
    let value = e.target.value

    this.setState({
      activeItem: {
        ...this.state.activeItem,
        title: value
      }
    })
  }
  handleSubmit(e) {
    e.preventDefault()

    var url = "https://todo-app-react-django.herokuapp.com/api/item/"
    const csrf_token = this.getCookie('csrftoken')
    var methd = "POST"

    if (this.state.isEditing) {
      url = "https://todo-app-react-django.herokuapp.com/api/item/" + this.state.activeItem.id + "/"
      methd = "PUT"
      this.setState({
        isEditing: false
      })
    }

    fetch(url, {
      method: methd,
      headers: {
        'Content-type': "application/json",
        'X-CSRFToken': csrf_token
      },
      body: JSON.stringify(this.state.activeItem)
    }).then(response => {
      this.fetchItems()
      this.setState({
        activeItem: {
          title: "",
          isDone: false
        }
      })
    })

  }

  handleDelete(item) {
    var url = "https://todo-app-react-django.herokuapp.com/api/item/" + item.id + "/"
    const csrf_token = this.getCookie('csrftoken')

    fetch(url, {
      method: "DELETE",
      headers: {
        'Content-type': "application/json",
        'X-CSRFToken': csrf_token
      },
      body: JSON.stringify(item)
    }).then(response => {
      this.fetchItems()
    })
  }

  itemCompleted(item) {
    item.isDone = !item.isDone

    const csrf_token = this.getCookie('csrftoken')
    var url = "https://todo-app-react-django.herokuapp.com/api/item/" + item.id + "/"

    fetch(url, {
      method: "PUT",
      headers: {
        'Content-type': "application/json",
        'X-CSRFToken': csrf_token
      },
      body: JSON.stringify(item)
    }).then(response => {
      this.fetchItems()
    })

  }

  render() {
    var items = this.state.ItemList
    var self = this
    return (
      < div className="container" >
        <h1 className="text-center display-6"><b>ToDo App</b></h1>
        {/* Form Wrapper Div */}
        < div id="task-container" >
          <div className="form-wrapper">

            <form onSubmit={this.handleSubmit}>
              <div className="flex-container" id="form">

                <div style={{ width: '90%' }}>
                  <input onChange={this.handleChange} className="form-control" type="text" value={this.state.activeItem.title} name="title" placeholder="Add a Task..." />
                </div>

                <div>
                  <button className="btn btn-outline-primary" type="submit">Add</button>
                </div>

              </div>
            </form>
          </div>

          {/* List Wrapper Div */}
          <div id="list-wrapper">
            {items.map(function (item) {
              return (
                <div key={item.id} className="flex-container" id="item">

                  <div onClick={() => self.itemCompleted(item)} style="flex: 70%">
                    {(item.isDone == false) ? <span className="task-wrapper">{item.title}</span> : <strike className="task-wrapper">{item.title}</strike>}
                  </div>

                  <div style="flex: 10%">
                    {(item.isDone == false) ? <button className="btn btn-outline-secondary" onClick={() => self.startEdit(item)}>Edit</button> :
                      <button className="btn btn-outline-secondary" onClick={() => self.startEdit(item)} disabled>Edit</button>}

                  </div>

                  <div style="flex: 20%">
                    <button className="btn btn-outline-danger" onClick={() => self.handleDelete(item)}>Delete</button>
                  </div>


                </div>
              )
            })}

          </div>
        </div>
        <div className="text-center">
          <span >Powered by React. <img src={logo} className="spinner" alt="logo" /></span>
        </div>
      </div >
    );
  }

}

export default App;